	module.exports = {"data":{"markdownRemark":{"html":"<h1>SIG Architecture Charter</h1>\n<p>This charter is a WIP.</p>\n<p>The Architecture SIG maintains and evolves the design principles of\nKubernetes, and provides a consistent body of expertise necessary to\nensure architectural consistency over time.</p>\n<p>The scope covers issues that span all the system's components, how\nthey fit together, how they interact, etc.</p>\n<p>Specific areas of focus include:</p>\n<ul>\n<li>\n<p>Defining the scope of the Kubernetes project</p>\n<ul>\n<li><a href=\"https://kubernetes.io/docs/concepts/overview/what-is-kubernetes/\">What is (and is not) Kubernetes</a></li>\n</ul>\n</li>\n<li>\n<p>Maintaining, evolving, and enforcing the deprecation policy</p>\n<ul>\n<li><a href=\"https://kubernetes.io/docs/reference/deprecation-policy/\">Deprecation policy</a></li>\n</ul>\n</li>\n<li>\n<p>Documenting and evolving the system architecture</p>\n<ul>\n<li><a href=\"../contributors/design-proposals/architecture/architecture.md\">Kubernetes Design and Architecture</a></li>\n</ul>\n</li>\n<li>Defining and driving necessary extensibility points</li>\n<li>\n<p>Establishing and documenting design principles</p>\n<ul>\n<li><a href=\"../contributors/design-proposals/architecture/principles.md\">Design principles</a></li>\n</ul>\n</li>\n<li>\n<p>Establishing and documenting conventions for system and user-facing APIs</p>\n<ul>\n<li><a href=\"/k8sC-sigs-demo/contributors/devel/api-conventions.md\">API conventions</a></li>\n</ul>\n</li>\n<li>Developing necessary technical review processes, such as the proposal and API review processes</li>\n<li>Driving improvement of overall code organization, including github orgs and repositories</li>\n<li>Educating approvers/owners of other SIGs (e.g., by holding office hours)</li>\n</ul>\n<p>Out of scope:</p>\n<ul>\n<li>Issues specific to a particular component or functional area, which would be the purview\nof some other SIG, except where they deviate from project-wide principles and conventions.</li>\n<li><a href=\"/k8sC-sigs-demo/contributors/design-proposals/release/versioning.md\">Release support policy</a>\nis owned by SIG Release</li>\n</ul>\n<p>TODO:</p>\n<ul>\n<li>Formalize decision processes</li>\n<li>Document initial reviewers and approvers</li>\n<li>Clarify criteria for areas out of scope for the SIG</li>\n<li>Document who owns client library, build, and release artifacts</li>\n<li>Document who owns conformance definition, profiles, etc.</li>\n</ul>"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-architecture/charter/"}}

